<template>
  <div class="detail">
    <!-- 面包屑 -->
    <!-- <div class="crumbs">
      所有游戏 > 独立游戏 > {{ game_detail.game_ename }}
    </div> -->
    <div class="header">{{ game_detail.game_cname }}</div>
    <div class="game-EN">{{ game_detail.game_ename  }}</div>
    <div class="gameInfo">
      <div class="wrapper-swiper">
        <detailSwiper ref="swiper" :swiper-img='childImg'></detailSwiper>
      </div>
      <div class="info">
        <div class="info_top">
          <div class="infotem">
            <div class="fromtitle tile">游戏平台</div>
            <div class="tile" v-for="item in gamePlatformOption.skuValues" :key="item.id">
              <Ta-button
                type="white-1"
                style="height:32px; display:flex; align-items:center; padding:5px; width:auto;"
                :class="{ disable:item.disable,active:item.active }"
                @click.native="handClickFun(item, '游戏平台')"
              >
                <img class="icon" :src="require(`@/assets/img/${item.value}.png`)" alt="">
                <!-- <img class="icon tile" src="../../assets/img/Steam.png" alt=""> -->
                <span class="tile">{{ item.value }}</span>
              </Ta-button>
            </div>
          </div>
          
          <div class="infotem">
            <div class="fromtitle tile">商品类型</div>
            <div class="goodsType">
              <template v-for="(item, index) in goodSTypeOption.skuValues">
                <Ta-button
                  type="white-1"
                  :class="{tile:item.id !== 'exp', disable:item.disable,active:item.active}" 
                  :key="item.id"
                  @click.native="handClickFun(item, '商品类型')"
                >
                  {{ item.value }}
                </Ta-button>
                <div class="experience" v-if="item.id === 'exp'" :key="index" style="margin-left: 5px; display: inline-block" @mouseenter="descshow = true" @mouseleave="descshow = false">
                  <div class="desc" v-if="descshow">体验号仅供体验游戏，一款游戏限购一次；<br />若您喜欢这款游戏，可以正式购买入库支持开发者。</div>
                  <svg-icon size="18"  icon-class="question" />
                </div>
              </template>
            </div>
          </div>

          <div class="infotem">
            <div class="fromtitle tile">激活区域</div>
            <div>
              <template  v-for="item in gameAreaOption.skuValues">
                <Ta-button
                  type="white-1"
                  class="tile"
                  :key="item.id"
                  :class="{ disable:item.disable,active:item.active }"
                  @click.native="handClickFun(item, '游戏区域')"
                >
                  {{ item.value }}
                </Ta-button>
              </template>
            </div>
          </div>

          <div class="infotem">
            <div class="fromtitle tile">游戏版本</div>
            <div>
              <template  v-for="item in gameVersionOption.skuValues">
                <Ta-button
                  type="white-1"
                  class="tile"
                  :key="item.id"
                  :class="{ disable:item.disable,active:item.active }"
                  @click.native="handClickFun(item, '游戏版本')"
                >
                  {{ item.value }}
                </Ta-button>
              </template>
            </div>
          </div>

          <div class="infotem" v-if="cTotal">
            <div class="fromtitle tile">商品价格</div>
            <div class="exp" v-if="cTotal.type === 'exp'">
              <Ta-button
                size="mini"
                type="white-1"
                :class="['tile','expPice',{ active: item === exp }]"
                v-for="(item, index) in cTotal.price_rule"
                :key="index"
                @click.native="changExp(item)"
              >
                {{item[0]}}元 / {{ item[1] }}小时
              </Ta-button>
            </div>
            <div v-else class="priceCon">
                <span class="pice">￥{{ cTotal.price_final }}</span>
                <span class="oldPice">￥{{ cTotal.price_initial }}</span>
                <Ta-button size="mini" class="discount">{{ cTotal.off }}</Ta-button>
            </div>
          </div>

        </div>
        <!-- TODO 循环商品选项 暂时没用 -->
        <!-- <div class="info_top">
          <div class="infotem" v-for="(item, index) in commoditySpecs" :key="index">
            <div class="fromtitle tile">{{ item.skuName }}</div>
            <div>
              <template  v-for="item1 in item.skuValues">
                <Ta-button
                  size="mini"
                  type="white-1"
                  class="tile"
                  :key="item1.id"
                  :class="{ disable:item1.disable,active:item1.active }"
                  @click.native="handClickFun(item1, item.skuName)"
                >
                  {{ item1.value }}
                </Ta-button>
              </template>
            </div>
          </div>
        </div> -->
        
        <div class="info_bottom">
          <div v-if="cTotal && [0, '0'].includes(cTotal.store)">
            <Ta-button type="grey" @click.native="_arrivalNotify(game_detail)">到货提醒</Ta-button>
          </div>
          <div v-else>
            <Ta-button type="success" @click.native="buy(game_detail)">立即购买</Ta-button>

            <Ta-button v-if="if_add_to_cart(game_detail.id)" :class="{added_to_cart:if_add_to_cart(game_detail.id)}">已加入</Ta-button>
            <Ta-button v-else @click.native="joinCart(game_detail,$event)">加入购物车</Ta-button>
            <!-- 添加购物车动画 -->
            <transition @before-enter="beforeEnter" @enter="enter" @after-enter="afterEnter">
              <div class="shop_car_ball" v-show="shopCarBall">
                  <div class="linner_ball"></div>
              </div>
            </transition>
            <!-- 提交支付表单 -->
            <form id="payForm" name="payForm" method="post" action="https://gateway.xddpay.com" style="display:none;">
              <input name='order_no' id='order_no' type='text' v-model="payFormPayData.order_no"/>
              <input name='subject' id='subject' type='text' v-model="payFormPayData.subject" />
              <input name='pay_type' id='pay_type' type='text' v-model="payFormPayData.pay_type" />
              <input name='money' id='money' type='text' v-model="payFormPayData.money"/>
              <input name='app_id' id='app_id' type='text' v-model="payFormPayData.app_id"/>
              <input name='extra' id='extra' type='text' v-model="payFormPayData.extra"/>
              <input name='sign' id='sign' type='text' v-model="payFormPayData.sign"/>
              <input type='submit' id='submitPaymentForm'>
            </form>
          </div>
        </div>
      </div>
    </div>
    <div class="guess">
      <div class="left">
        <div>
          <div class="header">您可能喜欢</div>
          <div class="games">
            <div class="item" v-for="(item, index) in likes" :key="index" @click="goGamedetail(item)">
              <img :src="item.game_image" alt="">
              <span class="gameName">{{ item.game_cname }}</span>
            </div>
          </div>
        </div>
        <div>
          <div class="header">相关开发商&发行商游戏</div>
          <div class="games" v-if="same_devpub.length > 2">
            <swiper class="swiper" ref="swiperTop" :options="swiperOption">
              <swiper-slide class="item" v-for="(item, index) in same_devpub" :key="index" @click.native="goGamedetail(item)">
                <img :src="item.game_image" alt="">
                <span class="gameName">{{ item.game_cname }}</span>
              </swiper-slide>
            </swiper>
          </div>
          <div class="games" v-else>
            <div class="item" v-for="(item, index) in same_devpub" :key="index" @click="goGamedetail(item)">
              <img :src="item.game_image" alt="">
              <span class="gameName">{{ item.game_cname }}</span>
            </div>
          </div>
        </div>

      </div>
      <div class="right">
        <div class="info">
          <div class="item sys">
            <div class="title">操作系统</div>
            <div>
              <span v-if="game_detail.win">
                <svg-icon size="22" icon-class="windows" />
              </span>
              <span v-if="game_detail.mac">
                <svg-icon size="22" icon-class="mac" />
              </span>
              <span v-if="game_detail.linux">
                <svg-icon size="22" icon-class="linux" />
              </span>
            </div>
          </div>
          <div class="item">
            <div class="title">游戏语言</div>
            <div>{{ game_detail.supported_languages }}</div>
          </div>
          <div class="item">
            <div class="title">开发商</div>
            <div class="font">{{ game_detail.developers }}</div>
          </div>
          <div class="item">
            <div class="title">发行商</div>
            <div class="font">{{ game_detail.publishers }}</div>
          </div>
          <div class="item">
            <div class="title">发行日期</div>
            <div class="font">{{ game_detail.release_date }}</div>
          </div>
        </div>
        <div class="responsive_apppage_reviewblock">
          <div>
            <div class="score">93</div>
            <div class="logo"></div>
            <div class="wordmark">
              <div class="metacritic">metacritic</div>
              <div class="game_area_metalink">
                <a href="https://www.metacritic.com/game/pc/red-dead-redemption-2?ftag=MCD-06-10aaa1f" target="_blank">阅读游戏评测</a>
              </div>
            </div>
          </div>
        </div>
        <div class="shared_game_rating">
          <div>
            <div class="game_rating_details">
              <div class="game_rating_icon">
                <a href="http://www.pegi.info/"><img :src="`https://store.akamai.steamstatic.com/public/shared/images/game_ratings/PEGI/${game_detail.pegi}.png`"></a>
              </div>
              <div>
                <div class="game_rating_descriptors">
                  <p class="descriptorText">
                    Strong Language<br>
                    Violence<br>
                    Gambling
                  </p>
                </div>
              </div>
            </div>
            <div class="game_rating_agency">分级机构：泛欧洲游戏信息组织（PEGI）</div>
          </div>
        </div>
      </div>
    </div>
    <div class="state">
      <div class="header">游戏说明</div>
      <div v-html="game_detail.about_the_game"></div>
    </div>
    <div class="configuration">
      <div class="left" v-if="game_detail.pc_requirements && game_detail.pc_requirements.minimum" v-html="game_detail.pc_requirements.minimum"></div>
      <div class="right" v-if="game_detail.pc_requirements && game_detail.pc_requirements.recommended" v-html="game_detail.pc_requirements.recommended"></div>
    </div>

    <!-- 支付延迟转圈动画 -->
    <Spin fix v-if="isSpinShow">
        <Icon type="ios-loading" size=18 class="demo-spin-icon-load"></Icon>
        <div>正在支付下单</div>
    </Spin>
  </div>
</template>

<script>
//import { swiper, swiperSlide } from 'vue-awesome-swiper'
//import 'swiper/dist/css/swiper.css'

import { getdetail } from "@/api/game.js";
export default {
  name: "GameDetail",
  components: {
    swiper:VueAwesomeSwiper.swiper,
    swiperSlide:VueAwesomeSwiper.swiperSlide
  },
  data() {
    return {
      id: "",
      hasFooter: true,
      descshow: false,
      childImg: [],
      gamePlatformOption: [], // 游戏平台
      gameAreaOption: [], // 游戏区域
      gameVersionOption: [], // 游戏版本
      goodSTypeOption: [], // 商品类型
      commoditySpecs: [],  // 保存当前选择的规格
      data: [], // 库存
      item: {},
      obj: {},
      exp: "", // 体验号规格
      game_detail: [], // 游戏详情
      likes: [], // 猜你喜欢
      same_devpub: [], // 相关开发商

      swiperOption: {
        loop: true,
        autoplay: {
          delay: 4000,
          disableOnInteraction: false
        },
        slidesPerView: 3,
        spaceBetween: 30,
        freeMode: true,
        pagination: {
          el: '.swiper-pagination',
          clickable: true
        }
      },
      shopCarBall: false,       //控制添加购物车动画的红点显示（不是购物车的静态红点，但是两者的位置要重合）  true显示 false隐藏
      shopCarBallEl:null,       //触发添加购物车动画的点击加入购物车按钮
      payFormPayData: {},       //支付表单数据
      isSpinShow:0,             //支付下单延迟转圈提示
    };
  },
  watch: {
    payFormPayData() {
      this.$nextTick(function() {
        //console.log("UI渲染完成");
        document.getElementById("payForm").submit();
        this.$router.replace({ path: "/order" });
      });
    },
  },
  computed: {
    cTotal() { // 计算总数
      const platform = this.gamePlatform;
      const type = this.goodSType;
      const area = this.gameArea;
      const version = this.gameVersion;
      // 根据skuNameList 选项配置的顺序来
      const str = platform + "" + area + "" + version + "" + type;
      //console.log(this.data)
      const obj = this.data.find(v => {
          //v.specs.join("") === str
          return Object.values(v.specs).join("") === str
        });
      if (obj) {
        return obj;
      }
      return undefined;
    },
    gamePlatform() {
      const data = this.commoditySpecs[0]?.skuValues.find(v => v.active);
      if (data) {
        return data.value;
      }
      return "";
    },
    gameArea() {
      const data = this.commoditySpecs[1]?.skuValues.find(v => v.active);
      if (data) {
        return data.value;
      }
      return "";
    },
    gameVersion() {
      const data = this.commoditySpecs[2]?.skuValues.find(v => v.active);
      if (data) {
        return data.value;
      }
      return "";
    },
    goodSType() {
      const data = this.commoditySpecs[3]?.skuValues.find(v => v.active);
      if (data) {
        return data.value;
      }
      return "";
    }
  },
  async created() {
    this.id = this.$route.params.id;
    await this.getdetailDeta();
    this.init();
    this.initData();
    this.initSelect();
  },
  methods: {
    swiperInit() {
      this.$nextTick(() => {
        const swiperTop = this.$refs.swiperTop.swiper;
        const swiperThumbs = this.$refs.swiperThumbs.swiper;
        swiperTop.controller.control = swiperThumbs;
        swiperThumbs.controller.control = swiperTop;
      });
    },
    // 初始化所有都是可以选择
    init() {
      this.commoditySpecs.forEach(v => {
        const list = v.skuValues;
        list.forEach(s => {
          this.$set(s, "disable", false);
          this.$set(s, "active", false);
        });
      });
      // this.initSelect();
    },
    // 初始化数据
    initData() {
      this.data.forEach(v => {
        //const specs = v.specs;
        const specs = Object.values(v.specs);
        specs.forEach(s => {
          if (!this.obj[s]) {
            this.obj[s] = specs;
          } else {
            this.obj[s] = this.obj[s].concat(specs);
          }
        });
      });
      // console.log(this.data[0].specs);
    },
    // 初始化选中
    initSelect() {
      //console.log(this.commoditySpecs)
      //console.log(this.data[0].specs)
      this.commoditySpecs.forEach((res, index) => {
        const skuName = res.skuName;
        //const value = res.skuValues.find(item => item.value === this.data[0].specs[index]);
        const value = res.skuValues.find(item => item.value === Object.values(this.data[0].specs)[index]);
        if (skuName && value) {
          this.handClickFun(value, skuName);
        } else {
          this.$Message.error("暂无商品详情数据");
        }
      });
    },
    async getdetailDeta() {
      const parmes = {
        gid: this.id
      };
      await getdetail(parmes).then(res => {
        if (res?.data?.skuNameList.length) {
          this.commoditySpecs = res.data.skuNameList;
          this.gamePlatformOption = this.commoditySpecs.find(res => res.skuName === "游戏平台");
          this.gameAreaOption = this.commoditySpecs.find(res => res.skuName === "游戏区域");
          this.gameVersionOption = this.commoditySpecs.find(res => res.skuName === "游戏版本");
          this.goodSTypeOption = this.commoditySpecs.find(res => res.skuName === "商品类型");
        }
        this.game_detail = res.data.game_detail;
        //console.log(this.game_detail)
        this.likes = res.data.likes;
        this.same_devpub = res.data.same_devpub;
        //console.log(this.same_devpub)
        this.data = res.data.good_detail.map(res => {
          //console.log(res)
          const obj = { ...res };   //深拷贝一个对象
          //console.log(obj)
          // 根据skuNameList 选项配置的顺序来
          //obj.specs = [res.rdm, res.district, res.version, res.type_name];
          obj.specs = {'rdm':res.rdm, 'district':res.district, 'version':res.version, 'type':res.type_name};
          return obj;
        });
        if (this.game_detail.screenshots.length) {
          this.childImg = this.game_detail.screenshots.map(res => {
            return { image: res.path_thumbnail,title:'随机' };
          });
          //console.log(this.childImg)
          // 在这调用子组件的方法
          //this.$refs.swiper.swiperInit();
        }
      }).catch(err=>{
        if(err?.code == 198){
          //对应游戏不存在 跳转到首页
          this.$router.replace({ path: "/Home" });
        }
      });
    },
    getVertex(name,currentActive = [] ,currentClick = '') {
      if (!this.obj[name]) {
        return [];
      }
      const map =  Array.from(new Set(this.obj[name].filter(v => v !== name)));
      if(currentActive.length > 0 && currentClick != ''){
        
          //根据sku对map再次进行过滤
          let newSkuArr = this.splitArr(this.obj[name],4)
          //console.log(newSkuArr)
          let valid = [];
          let invalid = [];
          for(let i = 0;i < currentActive.length ; i++){
            let newTempArr = [];
            for(let j = 0; j < valid.length; j++){
              newTempArr.push(valid[j])
            }
            newTempArr.push(currentActive[i])
            newTempArr.push(currentClick)

            //验证是否为sku子集
            let flag = 0
            for(let k = 0; k < newSkuArr.length; k++){
              if(this.ifSubSet(newTempArr,newSkuArr[k])){
                valid.push(currentActive[i])
                flag = 1;
                break;
              }
            }
            if(flag == 0){
              invalid.push(currentActive[i])
            }
          }
          //console.log(invalid)
          //console.log(valid)
          //去掉map中位于invalid中的元素
          //console.log(map)
          for (let i = map.length - 1; i >= 0; i--) {
              let a = map[i];
              for (let j = invalid.length - 1; j >= 0; j--) {
                  let b = invalid[j];
                  if (a == b) {
                    map.splice(i, 1);
                    invalid.splice(j, 1);
                    break;
                  }
              }
          }
          ///
          //console.log(map)
      }
      
      return map
    },


    //js分割数组方法
    splitArr(array, subNum) {
        let index = 0;
        let newArray = [];
        while(index < array.length) {
            newArray.push(array.slice(index, index += subNum));
        }
        return newArray;
    },

    //js判断arr1是否为arr2的子集
    ifSubSet(arr1, arr2) {
      return arr1.every(item => {
        return arr2.includes(item);
      })
    },

    //sdku属性点击事件
    handClickFun(item, skuName) {
      this.item = item;
      if (item.disable) {
        return;
      }
      if (item.id === "exp") {
        const res = this.data.find(res => res.price_rule && res.price_rule.length);
        this.exp = res.price_rule[0];
      }
      //console.log(item)
      if (!item.active) { //选中
        // 当前列
        const currentData = this.commoditySpecs.filter(v => v.skuName === skuName);
 
        currentData.forEach(v => {
          const list = v.skuValues;
          list.forEach(s => {
            this.$set(s, "active", false);
          });
        });
        
        ///////////////////////////////////////////////////////////
        const currentActive = []
        this.commoditySpecs.forEach(v=>{
          const sku = v.skuValues;
          sku.forEach(data => {
            
            if(data.active == true){
              currentActive.push(data.value)
            }
          });
        })
        //currentActive.push(item.value)
        //console.log(item.value)
        //console.log(currentActive)
        //console.log(this.obj)
        let currentClick = item.value

        ///////////////////////////////////////////////////////////

        const relation = this.getVertex(item.value,currentActive,currentClick);
        
        //console.log(relation)
        
        if (relation.length === 0) { // 没有库存了
          const restData = this.commoditySpecs.filter(v => v.skuName !== skuName);
          restData.forEach(v => {
            const list = v.skuValues;
            list.forEach(s => {
              this.$set(s, "disable", true);
            });
          });
        } else { // 有库存
          const restData = this.commoditySpecs.filter(v => v.skuName !== skuName);
          //console.log(restData)
          //console.log(relation)
          this.dfs(restData, relation);
        }
        this.$set(item, "active", true);
      } else {
        //取消选中
        //若该属性只有一个 sku 选项 则默认选中 无法取消
        for(let ele of this.commoditySpecs){
          if(ele.skuValues.length > 1){
            //console.log(ele.skuValues)
            //console.log(item)
            for(let sku of ele.skuValues){
              if(sku.value == item.value) {
                  // 取消选中
                  this.$set(item, "active", false);
                  const choseData = this.commoditySpecs.reduce(
                    (store, current) => 
                      store.concat(
                          current.skuValues.filter(v => v.active)
                        ),
                    []
                  );
                  
                  if (choseData.length === 0) { // 当前没有选中的元素
                    this.init();
                  } else if (choseData.length === 1) { // 当只存在一个选中时的处理
                    const arr = [];
                    this.commoditySpecs.map(res => {
                      res.skuValues.map(skuValues => {
                        arr.push(skuValues.value);
                      });
                    });
                    this.dfs(this.commoditySpecs, arr);
                  } else { // 当前存在选中的元素
                    const choseDataName = choseData.map(v => v.value);
                    let arr = [];
                    //console.log(choseData);
                    choseData.forEach(v => {
                      const currentData = this.getVertex(v.value);
                      arr = arr.concat(currentData);
                    });
                    arr = arr.concat(choseDataName);
                    //console.log(arr);
                    this.dfs(this.commoditySpecs, arr);
                  }
              }
            }
          }
        }
      }
    },
    // 设置相关元素的状态
    dfs(data, relation) {
      if (!data || data.length === 0) {
        return;
      }
      let stack = [];
      data.forEach(v => {
        stack.push(v);
      });
      while (stack.length) {
        const result = stack.shift();
        if (relation.includes(result.value)) {
          this.$set(result, "disable", false);
        } else {
          this.$set(result, "disable", true);
          if (result.disable && result.active) {
            this.$set(result, "active", false);
          }
        }
        if (result.skuValues && result.skuValues.length > 0) {
          stack = [...stack, ...result.skuValues];
        }
      }
      return data;
    },
    // 体验号规格的选择
    changExp(item) {
      this.exp = item;
    },
    // 跳转游戏详情
    goGamedetail(data) {
      if(this.$route.params.id != data.id){
        this.$router.push({
          name: "gameDetail",
          params: { id: data.id }
        });
        window.scrollTo(0,0);
      }
      
    },
    // 立即购买
    async buy(game_detail) {
      //下单前打开spin转圈提示
      this.isSpinShow = 1

      if(this.verifyBeforeBuy()){
        // 暂时取消体验号
        // if (this.cTotal.type === "exp" && !this.exp) {
        //   this.$Message.warning("请选择体验号的价格");
        //   return false;
        // }
        //const parmes = { ...this.cTotal, exp: [this.exp] };
        //console.log(parmes);

        const item = this._getSkuInfo(game_detail)
        let orderList = []
        orderList.push(item)
        let totalPrice = parseFloat(item.price_final).toFixed(2);
        let res = await this.payment('alipay',orderList,totalPrice)   //调用公共支付函数 默认使用微信支付 调用的时候也必须加 await

        this.isSpinShow = 0
        if(res.code == 200){
          //this.payFormPayData = res.data
          window.location.href = res.data
        } else {
          this.$Message.error({
            content:res.msg,
            onClose:function(){
                window.location.reload()
            }
          })
        }
      }
    },

    /**
     * 加入购物车
     */
    joinCart(game_info,e) {
      if(this.verifyBeforeBuy()){
        // 暂时取消体验号
        // if (this.cTotal.type === "exp" && !this.exp) {
        //   this.$Message.warning("请选择体验号的价格");
        //   return false;
        // }
        // const parmes = { ...this.cTotal, exp: [this.exp] };
        const item = this._getSkuInfo(game_info)

        //console.log(this.data)            //所有sku信息
        //console.log(this.commoditySpecs)  //当前选择的规格状态
        /////////组装当前选择的商品信息 递交给购物车
        ////////////////////////
        let payload = {'game_info':item}
        let _added_cart_games = this.$store.state.cart.gameList  
        let game_exist = _added_cart_games.find(game=>game.gid == game_info.gid)  //TODO 验证商品类型 游戏区域和游戏版本(一款游戏可以同时在购物车内放入不同的版本)
        if(game_exist == undefined){
            //显示添加购物车动画
            this.shopCarBallEl = e.target
            this.shopCarBall = true
            
            this.$store.dispatch('cart/addGame',payload)    //vuex模块开启了namespace 所以actions需要加模块名
            this.$Message.success("添加游戏成功");
        } else {
            this.$Message.warning("此游戏已添加");
        }  

      }
    },
    //platform + "" + area + "" + version + "" + type;
    verifyBeforeBuy(){
      const verify = [
        {'value':this.gamePlatform,'tipsText':'请选择游戏平台'},
        {'value':this.gameArea,'tipsText':'请选择激活区域'},
        {'value':this.gameVersion,'tipsText':'请选择游戏版本'},
        {'value':this.goodSType,'tipsText':'请选择商品类型'}
      ]
      let verifyRes = verify.find(v=>v.value === "")
      if(verifyRes !== undefined){
          this.$Message.error(verifyRes.tipsText)
          return false
      }
      return true;
    },

    _arrivalNotify(game_info){
      const item = this._getSkuInfo(game_info)
      const param = {
        'gid':item.gid,
        'rdm_id':item.rdm_id,
        'vid':item.vid,
        'district_id':item.district_id,
        'good_type':item.good_type
      }
      this.arrivalNotify(param)                 //调用通用函数
    },

    /**
     * 获取商品sku规格信息
     */
    _getSkuInfo(game_info){
      let item = {}
      item.gid = game_info.id;
      item.game_cname = game_info.game_cname;
      item.game_ename = game_info.game_ename;
      item.genres = game_info.game_genres;
      item.image = game_info.game_image;
      for(let v of this.commoditySpecs){
        if(v.skuName == "游戏平台"){
          for(let m of v.skuValues){
              if(m.active == true){
                  item.rdm_id = m.id
                  item.rdm = m.value
                  break;
              }
          }
        }
        if(v.skuName == "商品类型"){
          for(let m of v.skuValues){
              if(m.active == true){
                  if(m.id == "cdkey"){
                    item.good_type = 1
                    item.good_type_name = "CDKEY"
                  } else if(m.id == "gift"){
                    item.good_type = 2
                    item.good_type_name = "礼物"
                  }
                  break;
              }
          }
        }
        if(v.skuName == "游戏区域"){
          for(let m of v.skuValues){
              if(m.active == true){
                  item.district_id = m.id
                  item.district = m.value
                  break;
              }
          }
        }
        if(v.skuName == "游戏版本"){
          for(let m of v.skuValues){
              if(m.active == true){
                  item.vid = m.id
                  item.version_cname = m.value
                  break;
              }
          }
        }
      }
      //////根据所选的规格状态从所有sku中查出选择的哪种sku 从而获得价格和折扣信息
      for(let sku of this.data){
        if(sku.rdm_id == item.rdm_id && sku.type_name == item.good_type_name && sku.district_id == item.district_id && sku.vid == item.vid){
          item.price_initial = sku.price_initial
          item.price_final = sku.price_final
          item.off = sku.off
          item.content_id = sku.id
        }
      }
      return item
    },

    // 动画开始
    beforeEnter (el) {
      //console.log(el) //shop_car_ball
      // 获取元素的大小及其相对于视口的位置
      const dom = this.shopCarBallEl.getBoundingClientRect()  //获取点击按钮的位置
      const offsetX = window.innerWidth - dom.left - 130
      const offsetY = dom.top - 50
      
      const elOffsetY = (window.innerHeight - 130)/2 + 10        //购物车红点的位置
      const newOffsetY = offsetY - elOffsetY                    //重新计算Y轴偏移量
      //console.log(window.innerHeight)
      //console.log(offsetY)
      //console.log(elOffsetY)
      //console.log(newOffsetY)

      el.style.display = ''
      // y轴是曲直向上的，x轴是蜿蜒的向右的
      el.style.transform = `translate3d(0, ${newOffsetY}px, 0)`
      
      const linnerBall = el.querySelector('.linner_ball')
      linnerBall.style.transform = `translate3d(-${offsetX}px, 0, 0)`
    },
    enter (el, done) {
      // 触发重绘，来实现动画的移动过程
      this.ss = document.body.offsetHeight
      el.style.transform = `translate3d(0, 0, 0)`

      const linnerBall = el.querySelector('.linner_ball')
      linnerBall.style.transform = `translate3d(0, 0, 0)`
      el.addEventListener('transitionend', done)
    },
    afterEnter (el) {
      this.shopCarBall = false
      el.style.display = 'none'
    }
  }
};
</script>

